/* Color Palette */
:root {
  --color-main: #10143b;
  --color-secondary: #f4feff;
  --color-btn-primary: #cfa348;
  --color-btn-secondary: #f2f3ff;
  --color-btn-nav: #10143b;
  --color-footer: #0c0f2b;
  --color-plain: #ffffff;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Typography */
::selection {
  background-color: #10143b;
  background-color: var(--color-main);
  color: #ffffff;
  color: var(--color-plain);
}

html {
  font-size: 62.5%;
}

body {
  /* color: var(--color-body); */
  font-family: "Hind Vadodara", sans-serif;
  font-size: 1.6rem;
  line-height: 2.397rem;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
li,
a {
  color: #ffffff;
  color: var(--color-plain);
}

h1 {
  font-size: 5.6rem;
  font-weight: 600;
  line-height: 6.261rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 0.8rem;
  font-weight: 400;
  line-height: 1.198rem;
}

p {
  font-size: 1.6rem;
  font-weight: 400;
}

