    background: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(/school-scout-fe-pjt-56/static/media/login.31060666.png);
    height: 602px;
    width: 368px;
}

.app-signup-img {
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(/school-scout-fe-pjt-56/static/media/signup.7c71c755.png);
      /* height: 602px; */
      width: 368px;
}

.app-login-heading, .app-sgnup-heading {
    color: #121212;
    color: var(--color-main);
    font-size: 4rem;
    font-weight: 400;
    line-height: 5rem;
    /* margin-top: 60px; */
    margin-bottom: 0;
    text-align: center;
}

.app-sgnup-heading {
  line-height: 45px;
  margin-top: 0;
}

.app-login-form {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 0 58px 40px 60px;
    text-align: center;
}

.app-sgnup-input-grp {
  display: -webkit-flex;
  display: flex;
  grid-column-gap: 32px;
  -webkit-column-gap: 32px;
          column-gap: 32px;
}

.app-login-input {
    font-size: 16px;
    line-height: 24px;
    padding-top: 4rem;
    padding-bottom: 0.8rem;
    max-width: 400px;
    width: 100%;
    border: 0;
    border-bottom: 1px solid #DCDCDC;
    outline: 0;
}

.app-sgnup-input-left, .app-login-input-left {
    margin-left: 0;
    margin-right: -3px;

}

.app-login-pwd-mask {
    margin-left: -14px;
    -webkit-align-self: flex-end;
            align-self: flex-end;
    margin-bottom: 8px;
}

.app-login-pwd-mask:hover,
.app-login-fgt-pwd:hover,
.app-login-btn:hover,
.app-login-btn-google:hover,
.app-sgnup-btn:hover {
    cursor: pointer;
}

.app-login-fgt-pwd, .app-sgnup-tnc {
    margin-bottom: 32px;
    text-align: end;
    color: #263AFF;;
    text-decoration: none;
}

.app-sgnup-tnc {
  color: #cfa348;
  color: var(--color-btn-primary);
}

.app-sgnup-tnc {
  margin-bottom: 0;
  margin-left: 3px;
  font-size: 14px;
  line-height: 21px;

}

.app-login-btn, 
.app-login-btn-google {
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    border: 0;
    font-size: 16px;
    line-height: 24px;
}

.app-login-btn,
.app-sgnup-btn  {
  border-radius: 10px;
  background-color: #cfa348;
  background-color: var(--color-btn-primary);
}

.app-login-btn {
    padding: 16px 181px;
}

.app-sgnup-btn {
  /* padding: 16px 174px; */
  font-size: 16px;
    line-height: 24px;
    width: 100%;
}

.app-login-btn-google {
    -webkit-align-items: center;
            align-items: center;
    background-color: white;
    border: 1px solid #C0C6FC;
    border-radius: 15px;
    color: #121212;
    /* padding: 16px 114px 16px 114px; */
    padding: 16px 0 16px 0;
    display: -webkit-flex;
    display: flex;
    grid-column-gap: 16px;
    -webkit-column-gap: 16px;
            column-gap: 16px;
    align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.lbl-or {
    color: #121212;
    display: inline-block;
    font-size: 16px;
    line-height: 24px;
    margin: 24px 0;
}

.app-login-pwd-container {
    display: -webkit-flex;
    display: flex;
}

.app-login-sgnup {
    color: #121212;
    color: var(--color-main);
    margin-top: 24px;
}

.lbl-or, .app-login-sgnup,
.app-login-sgnup-lnk  {
    font-size: 16px;
    line-height: 24px;
}

.app-login-sgnup-lnk {
    color: #cfa348;
    color: var(--color-btn-primary);
}

.app-sgnup-chkbx {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  margin-top: 14px;
    margin-bottom: 29px;
    font-size: 14px;
    line-height: 21px;
}

.app-sgnup-chkbx input {
  margin-right: 3px;
}

.app-sgnup-input {
  font-size: 16px;
  line-height: 24px;
  padding-top: 3.2rem;
  padding-bottom: 0.8rem;
  max-width: 400px;
  width: 100%;
  border: 0;
  border-bottom: 1px solid #DCDCDC;
  outline: 0;
}

.app-sgnup-form {
  padding: 60px 64px 60px 64px;
}

.app-login-sgnup-lnk {
  color: #cfa348;
  color: var(--color-btn-primary);
  text-decoration: none;
}

.app-login-form {
  padding: 0;
}
